import styles from './Profile.module.css';
import { users } from '../../utils/mockUsers';
import { TypeUser } from '../../types/types';
import { useLocation } from 'react-router-dom';

function Profile(): React.JSX.Element {
  const location = useLocation();
  const lastSegment = location.pathname.split('/').pop();

  // Ensure we safely handle invalid segments
  const userId = lastSegment ? Number(lastSegment) : null;

  const currentUser = userId ? users?.find((item) => item.id === userId) : null;

  // Return early if currentUser is not found
  // if (!currentUser) {
  //   return <p>User not found.</p>;
  // }

  const boss = users.find((item) => item.id === currentUser?.bossId);
  const team = users.filter((item) => item.bossId === currentUser?.id);

  return (
    <section className={styles.section}>
      <img
        className={styles.user_avatar}
        src={`https://randomuser.me/api/portraits/${currentUser?.gender}/${currentUser?.id}.jpg`}
        alt="avatar"
      />
      <div className={styles.user_info}>
        <h3>{`${currentUser?.first_name} ${currentUser?.last_name}`}</h3>
        <p>{`Position: ${currentUser?.position}`}</p>
        <p>{`Grade: ${currentUser?.grade}`}</p>
      </div>
      <div className={styles.text_container}>
        {boss && (
          <>
            <h4>Boss:</h4>
            <div className={styles.text_container}>
              <p>{boss.first_name}</p>
              <p>{boss.last_name}</p>
            </div>
          </>
        )}
      </div>
      <h4>Team:</h4>
      <div>
        {team.length > 0 ? (
          team.map((item) => (
            <div key={item.id} className={styles.text_container}>
              <p>{item.position}</p>
              <h4>{`${item.first_name} ${item.last_name}`}</h4>
            </div>
          ))
        ) : (
          <p>No team members found.</p>
        )}
      </div>
    </section>
  );
}

export default Profile;
